import { Overlay } from 'react-bootstrap';
import { MapContainer, TileLayer, ImageOverlay} from 'react-leaflet'

import {CENTER_CANTABRIA} from './resources/constants'

const xicon = new L.Icon({
  iconUrl: 'black_marker.png',
  iconRetinaUrl: 'black_marker.png',
  iconAnchor: [8,8],
  popupAnchor: [0, 0],
  iconSize: [16, 16],
  className: 'leaflet-div-icon'
});

export default function ResultsMap(props) {
    return (
        <MapContainer center={CENTER_CANTABRIA} zoom={10} scrollWheelZoom={true} zoomControl={false} dragging={true}>   
          <TileLayer
            attribution='&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
            url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
          />
          {
            Object.keys(props.isos).map(function(key) {
              return <ImageOverlay
                  key={key}
                  url={props.isos[key].svg}
                  bounds={[
                    [43.518, -4.8522],
                    [42.76, -3.15]
                  ]}
                  opacity={1.0}
                  zIndex={1000}
                />

            })
          }

        </MapContainer>
    );
}