import React, { Component} from 'react';

import {Navbar, Nav} from 'react-bootstrap'

import LanguageSelector from './LanguageSelector';
import {Multiselect} from 'multiselect-react-dropdown'

import {MAP_INDEX} from './resources/map-index'

export default class TopNavBar extends Component {

    constructor(props) {
        super(props);
        this.state = {
            locale: props.config.locale
        }
    }

    handleChange(selectedList, selectedItem) {
        this.props.handleSelectIsos(selectedList)
    }

    handleChangeLanguage(event) {
        if(this.props.loc.availableLocales.includes(event.target.id)) {
          this.props.loc.locale = event.target.id
          this.props.config.locale = event.target.id
          this.setState({locale : this.props.config.locale})
        }
    }

    render() {
        return (
            <Navbar fixed="top"  bg="dark" expand="lg" variant="dark">
                <Navbar.Brand>
                    <img src="./unicorn.png" alt={this.props.loc.get("brand_alt")}/>
                </Navbar.Brand> 
                <Navbar.Brand className={'main-brand'}>{this.props.loc.get("brand_name")}</Navbar.Brand>
                <Navbar.Toggle aria-controls="basic-navbar-nav" />  
                
                <Navbar.Collapse id="basic-navbar-nav">  
                    <Nav className="mr-auto">
                    <Multiselect
                        placeholder={this.props.loc.get("all_fem")} 
                        groupBy="category"
                        displayValue={this.state.locale=="esp" ? "name_esp" : "name_ast"}
                        options={MAP_INDEX}
                        showCheckbox={true} 
                        closeOnSelect={false}
                        onSelect={this.handleChange.bind(this)} 
                        onRemove={this.handleChange.bind(this)} 
                        style={{'width': '400px'}}
                    />
                    </Nav>              
                    
                </Navbar.Collapse>

                <Nav className="ml-auto">
                    <Navbar.Collapse id="basic-navbar-nav">            
                        <LanguageSelector onLanguageChanged={this.handleChangeLanguage.bind(this)} title={this.props.loc.get(this.state.locale)} {...this.props} />
                    </Navbar.Collapse>
                </Nav>      

            </Navbar>
            )
        }
    }