import { NavDropdown } from 'react-bootstrap';

export default function LanguageSelector(props) {
    const availableLocales = props.loc.availableLocales.map(l =>  ({key: l, localized: props.loc.get(l)}))
                                                       .sort((a,b) => a.localized.localeCompare(b.localized))
    return (
        <NavDropdown title={props.title} onClick={props.onLanguageChanged} alignRight>
        {
          availableLocales.map(function(locale) {
            return <NavDropdown.Item key={locale.key} id={locale.key}>{locale.localized}</NavDropdown.Item>
          })
        }
        </NavDropdown>
    )
}