import React, { Component} from 'react';

import './App.css';

import {Container} from 'react-bootstrap';

import TopNavBar from './TopNavBar'
import ResultsMap from './ResultsMap';
import { MAP_INDEX } from './resources/map-index';

export default class App extends Component {

  constructor(props) {
    super(props);
    
    this.state = {
        locale: props.loc.locale,
        isos: MAP_INDEX
    }
  }        

  handleSelectIsos(selectedIsos) {
    this.setState({isos: selectedIsos})
  }

  render() {
    return (
      <Container>
                  <TopNavBar 
                      locale={this.state.locale}
                      handleSelectIsos={this.handleSelectIsos.bind(this)}
                      {...this.props} />
                  <ResultsMap 
                      isos={this.state.isos}
                      {...this.props} />
      </Container>
    );
  }

}

